/**
 * Grid panel layout metadata
 */
export class GridPanelLayoutEditorMetadata {
  //######################### constructor #########################
  constructor() {
    //######################### public properties - implementation of LayoutEditorMetadataDescriptor #########################

    /**
     * @inheritdoc
     */
    this.metaInfo = {
      name: 'Grid',
      description: 'Layout component with rows, and columns',
      group: 'Layout'
    };
    /**
     * @inheritdoc
     */

    this.getDescendants = options => options?.cells ?? [];

    Object.freeze(this);
  }

}